.btn-blue {
  background: rgba(40, 113, 255, 1);
  border-radius: 0.1875rem;
  span {
    min-width: auto;
    font-family: PTSans-Caption;
    font-size: 1rem;
    line-height: 130%;
    padding: 1.25rem;
    color: #ffffff;
  }
  &:hover {
    background: rgba(90, 146, 255, 1);
  }
}
.btn-white {
  background: rgb(255, 255, 255);
  border-bottom: 0.125rem solid rgb(239, 239, 239) !important;
  border-radius: 0.1875rem;
  &:hover {
    background: #dfdfdf;
  }
  span {
    min-width: auto;
    font-family: PTSans-Caption;
    font-size: 1rem;
    line-height: 130%;
    padding: 1.25rem;
    color: rgb(51, 51, 51);
  }
}
h2 {
  font-size: 2.5rem;
  line-height: 120%;
  text-transform: uppercase;
  color: rgb(51, 51, 51);
  margin: 0;
  .text_color {
    color: rgb(40, 113, 255);
  }
}
.subtitle {
  font-size: 1.875rem;
  line-height: 130%;
  font-weight: 700;
  color: rgb(51, 51, 51);
  margin: 1.5rem 0 0 0;
}
.agreement-check {
  input {
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
  input:checked ~ label {
    &:after {
      background-image: url("../img/check.svg");
      background-color: #2871ff;
    }
  }
  label {
    font-size: 0.75rem;
    padding-left: 1.875rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 1.125rem;
      height: 1.125rem;
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      border: 0.0625rem solid #2871ff;
      border-radius: 0.3125rem;
    }
  }
}
.slick-arrow {
  width: 4.0625rem;
  height: 4.0625rem;
  background: #ffffff;
  border: 0.0625rem solid rgba(0, 0, 0, 0.1);
  &:hover {
    border-color: #2871FF;
    svg path {
      stroke: #2871FF;
    }
  }
  svg {
    width: 0.5625rem;
    height: auto;
    min-width: unset;
    min-height: unset;
  }
}
.navigation {
  padding: 3.125rem 0;
  ul {
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    li {
      font-size: 1.5rem;
      line-height: 130%;
    }
    li a:hover {
      color: rgb(40, 113, 255);
    }
  }
}
.left-anim {
  transform: translate(-60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.popup {
  background: rgba(255, 255, 255, .7);
}


@media screen and (max-width: 1600px) {
  h2 {
    font-size: 2.25rem;
  }
  .subtitle {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 1399px) {
  h2 {
    font-size: 1.75rem;
  }
  .subtitle {
    font-size: 1.25rem;
  }
}

@media screen and (max-width: 1023px) {
  .subtitle {
    font-size: 1rem;
  }
  .slick-arrow {
    width: 3.125rem;
    height: 3.125rem;
  }
}

@media screen and (max-width: 576px) {
  .btn-blue {
    span {
      font-size: 0.875rem;
    }
  }
  .btn-white {
    span {
      font-size: 0.875rem;
    }
  }
}