html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}

body {
  font-family: PTSans-Caption, sans-serif;
}

.fixed-body {
  overflow: hidden;
}

.wrapper {
  position: relative;
  overflow: hidden;
}

.container-fluid {
  max-width: 1912px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: PTSans-Caption;
  font-weight: 700;
}

.btn {
  display: inline-flex !important;
  justify-content: center;
}

img {
  object-fit: cover;
}