.section-group {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% + 18.75rem);
    background-image: url("../img/section-group__bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    pointer-events: none;
  }
}
.main-section {
  z-index: 1;
  padding: 12.5rem 0 4.6875rem;
  &__wrap {
    max-width: 54.0625rem;
    p {
      font-size: 2.125rem;
      line-height: 120%;
      letter-spacing: 0%;
      text-transform: uppercase;
      color: #ffffff;
      margin: 1.5rem 0 0 0;
    }
  }
  h1 {
    font-size: 4rem;
    line-height: 105%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;
    margin: 0;
  }
  &__text {
    max-width: 35.9375rem;
    font-size: 1.875rem;
    line-height: 130%;
    font-weight: 700;
    letter-spacing: 0%;
    color: #ffffff;
    margin-top: 1.5rem;
  }
  &__group {
    max-width: 35.9375rem;
    display: flex;
    align-items: center;
    gap: 5rem;
    background: rgb(255, 255, 255);
    border-radius: 1.25rem;
    padding: 1.125rem;
    margin-top: 5.625rem;
  }
  &__btn {
    flex-shrink: 0;
    span {
      padding: 1.25rem 2.875rem !important;
      overflow: unset;
    }
    i {
      position: absolute;
      top: 0.3125rem;
      left: calc(100% - 3.125rem);
      width: 7.5rem;
      img {
        width: 100%;
      }
    }
  }
  &__info {
    font-size: 1rem;
    line-height: 130%;
    letter-spacing: 0%;
    span {
      font-weight: 700;
      color: rgb(40, 113, 255);
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 2.5rem / 3));
    gap: 1.25rem;
    margin-top: 4.125rem;
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    background: rgb(255, 255, 255);
    border-radius: 1.25rem;
    padding: 1.25rem;
    &-img {
      max-width: 7.25rem;
      flex-shrink: 0;
    }
    &-text {
      max-width: 23.75rem;
      font-size: 1.25rem;
      line-height: 130%;
      color: rgb(51, 51, 51);
      strong {
        font-weight: 700;
      }
    }
  }
  &__img2 {
    position: absolute;
    top: 0;
    left: 0;
    width: 65rem;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    top: 0;
    right: 0;
    width: 22.5rem;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img4 {
    position: absolute;
    right: 0;
    bottom: -28rem;
    width: 52.5rem;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.quiz {
  z-index: 1;
  padding: 4.6875rem 0;
  .container-fluid {
    z-index: 3;
  }
  &__inner {
    max-width: 48.75rem;
    min-height: 54.6875rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #ffffff;
    border-radius: 1.875rem;
    padding: 3.75rem;
    margin-left: auto;
  }
  &__inner--active {
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(1.875rem);
  }
  &-step {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(0, -2rem);
    opacity: 0;
    pointer-events: none;
  }
  &-step--active {
    position: static;
    transform: unset;
    opacity: 1;
    pointer-events: auto;
  }
  &-step1 {
    .quiz-checkboxes {
      grid-template-columns: repeat(3, calc(100% / 3 - 2.5rem / 3));
      gap: 1.25rem;
    }
    .quiz-checkbox {
      border-radius: 1.25rem;
    }
    .quiz-checkbox__text {
      justify-content: space-between;
      gap: 0.3125rem;
    }
  }
  &-checkboxes {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
    margin-top: 2rem;
  }
  &-checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ .quiz-checkbox__label {
      border-color: #2871FF;
      .quiz-checkbox__text span {
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        background-color: #2871FF;
      }
    }
    &__label {
      width: 100%;
      height: 100%;
      border: 0.0625rem solid rgba(0, 0, 0, 0.1);
      border-radius: 0.375rem;
      padding: 0.625rem;
    }
    &__img {
      img {
        width: 100%;
      }
    }
    &__text {
      display: flex;
      gap: 0.625rem;
      font-size: 1rem;
      line-height: 130%;
      color: rgb(51, 51, 51);
      margin-top: 0.625rem;
    }
    &__text span {
      width: 1.4375rem;
      height: 1.4375rem;
      flex-shrink: 0;
      border: 0.0625rem solid rgba(0, 0, 0, 0.2);
      border-radius: 50%;
      margin-top: -0.125rem;
    }
  }
  &-nav {
    display: flex;
    justify-content: flex-end;
    align-items: end;
    gap: 0.5rem;
    margin-top: 3.125rem;
  }
  &-prev {
    position: relative;
    border-radius: 0.1875rem;
    gap: 1rem;
    padding-left: 1.25rem !important;
    opacity: 0;
    pointer-events: none;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: calc(100% + 0.125rem);
      background: rgb(223, 223, 223);
      border-radius: 0.1875rem;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgb(255, 255, 255);
      border-radius: 0.1875rem;
      pointer-events: none;
    }
    &:hover {
      &:after {
        display: none;
      }
      &:before {
        background: rgb(223, 223, 223);
      }
    }
    span {
      position: relative;
      z-index: 2;
      min-width: auto;
      padding-left: 0;
      font-size: 1rem;
      line-height: 130%;
      color: rgb(51, 51, 51);
    }
    i {
      position: relative;
      z-index: 2;
      width: 0.375rem;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &-prev--active {
    opacity: 1;
    pointer-events: auto;
  }
  &-next {
    padding-right: 1.25rem !important;
    span {
      padding-right: 0;
    }
    i {
      position: relative;
      z-index: 2;
      width: 0.375rem;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &-form {
    display: none;
  }
  &-form--active {
    display: block;
  }
  &-error {
    display: none;
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    color: red;
    margin-top: 0.625rem;
  }
  &-error--active {
    display: block;
  }
  &__img {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    width: 83.375rem;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: -19rem;
    width: 78.125rem;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    z-index: 1;
    right: 0;
    bottom: -28rem;
    width: 103.4375rem;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img4 {
    position: absolute;
    left: 0;
    bottom: -10rem;
