.footer {
  position: absolute;
  z-index: 5;
  left: 0;
  bottom: 0;
  width: 100%;
  color: #ffffff;
  padding: 1.5rem 0;
}
.footer-top {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2.75rem;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 2.75rem;
  }
  &__logo {
    width: 13.8125rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    &:nth-child(1) {
      gap: 2.75rem;
    }
    &:nth-child(2) {
      flex-shrink: 0;
      gap: 1.5rem;
    }
  }
  &__location,
  &__email {
    display: flex;
    align-items: center;
    gap: 0.625rem;
    &:hover {
      i {
        background: rgb(40, 113, 255);
        border-color: rgb(40, 113, 255);
        svg path {
          fill: #fff;
        }
      }
      p {
        color: rgb(40, 113, 255);
        transition: all 0.3s;
      }
    }
    i {
      width: 2.625rem;
      height: 2.625rem;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      border: 0.1875rem solid rgba(255, 255, 255, 0.2);
      border-radius: 50%;
      transition: all 0.3s;
      svg {
        max-width: 1.5rem;
      }
    }
    p {
      font-size: 1rem;
      line-height: 130%;
      margin: 0;
    }
    strong {
      display: block;
      font-size: 1.125rem;
      font-weight: 700;
    }
  }
  &__phones {
    display: flex;
    flex-direction: column;
    gap: 0.3125rem;
  }
  &__phone {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: rgb(40, 113, 255);
    }
  }
}
.footer-bottom {
  font-size: 0.875rem;
  line-height: 130%;
  padding-top: 3.75rem;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1rem 2rem;
  }
  &__right {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 1rem 10rem;
  }
  &__logo {
    width: 13.875rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    display: flex;
    align-items: center;
    gap: 1rem;
    p {
      margin: 0;
    }
  }
  &__politics {
    &:hover {
      color: rgb(40, 113, 255);
    }
  }
}

@media screen and (max-width: 1800px) {
  .footer-bottom {
    &__right {
      gap: 1rem 2rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .footer-top {
    &__inner {
      gap: 1.5rem;
    }
    &__right {
      gap: 2rem;
    }
    &__group {
      &:nth-child(1) {
        gap: 1.5rem;
      }
    }
    &__location,
    &__email {
      p {
        font-size: 0.875rem;
      }
      strong {
        font-size: 1rem;
      }
    }
    &__phone {
      font-size: 1rem;
    }
    &__btn {
      span {
        padding: 1rem 1.25rem;
      }
    }
  }
  .footer-bottom {
    padding-top: 2rem;
  }
}

@media screen and (max-width: 1180px) {
  .footer-top {
    &__group {
      &:nth-child(2) {
        flex-direction: column;
        align-items: start;
        gap: 0.625rem;
      }
    }
    &__btn {
      background: transparent;
      span {
        padding: 0;
      }
      &:hover {
        background: transparent;
        span {
          color: rgb(40, 113, 255);
        }
      }
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer-top {
    &__logo {
      width: 11.25rem;
    }
    &__group {
      &:nth-child(1) {
        flex-direction: column;
        align-items: start;
        gap: 0.625rem;
      }
    }
    &__location,
    &__email {
      &:hover {
        i {
          background: transparent;
          svg path {
            fill: rgb(40, 113, 255);
          }
        }
      }
      i {
        width: auto;
        height: auto;
        background: transparent;
        border: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__group {
      &:nth-child(1) {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 499px) {
  .footer-top {
    &__inner {
      gap: 1rem;
    }
    &__logo {
      width: 8.75rem;
    }
    &__phone {
      font-size: 0.875rem;
    }
  }
}