#header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  font-family: Roboto;
  color: #ffffff;
  padding: 1.25rem 0;
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2.75rem;
  }
  .header__right {
    display: flex;
    align-items: center;
    gap: 2.75rem;
  }
  .header__logo {
    width: 13.8125rem;
    display: block;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  .header__group {
    display: flex;
    align-items: center;
    &:nth-child(1) {
      gap: 2.75rem;
    }
    &:nth-child(2) {
      flex-shrink: 0;
      gap: 1.5rem;
    }
  }
  .header__location,
  .header__email {
    display: flex;
    align-items: center;
    gap: 0.625rem;
    &:hover {
      i {
        background: rgb(40, 113, 255);
        border-color: rgb(40, 113, 255);
        svg path {
          fill: #fff;
        }
      }
      p {
        color: rgb(40, 113, 255);
        transition: all 0.3s;
      }
    }
    i {
      width: 2.625rem;
      height: 2.625rem;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      border: 0.1875rem solid rgba(255, 255, 255, 0.2);
      border-radius: 50%;
      transition: all 0.3s;
      svg {
        max-width: 1.5rem;
      }
    }
    p {
      font-size: 1rem;
      line-height: 130%;
      margin: 0;
    }
    strong {
      display: block;
      font-size: 1.125rem;
      font-weight: 700;
    }
  }
  .header__phones {
    display: flex;
    flex-direction: column;
    gap: 0.3125rem;
  }
  .header__phone {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: rgb(40, 113, 255);
    }
  }
}
.header2 {
  color: rgb(51, 51, 51) !important;
}

@media screen and (max-width: 1399px) {
  #header {
    .header__inner {
      gap: 1.5rem;
    }
    .header__right {
      gap: 2rem;
    }
    .header__group {
      &:nth-child(1) {
        gap: 1.5rem;
      }
    }
    .header__location,
    .header__email {
      p {
        font-size: 0.875rem;
      }
      strong {
        font-size: 1rem;
      }
    }
    .header__phone {
      font-size: 1rem;
    }
    .header__btn {
      span {
        padding: 1rem 1.25rem;
      }
    }
  }
}

@media screen and (max-width: 1180px) {
  #header {
    .header__group {
      &:nth-child(2) {
        flex-direction: column;
        align-items: start;
        gap: 0.625rem;
      }
    }
    .header__btn {
      background: transparent;
      span {
        padding: 0;
      }
      &:hover {
        background: transparent;
        span {
          color: rgb(40, 113, 255);
        }
      }
    }
  }
  .header2 {
    .header__btn {
      span {
        color: rgb(51, 51, 51);
        padding: 0;
      }
    }
  }
}

@media screen and (max-width: 1023px) {
  #header {
    padding: 1rem 0;
    .header__logo {
      width: 11.25rem;
    }
    .header__group {
      &:nth-child(1) {
        flex-direction: column;
        align-items: start;
        gap: 0.625rem;
      }
    }
    .header__location,
    .header__email {
      &:hover {
        i {
          background: transparent;
          svg path {
            fill: rgb(40, 113, 255);
          }
        }
      }
      i {
        width: auto;
        height: auto;
        background: transparent;
        border: none;
      }
    }
  }
  .header2 {
    color: #ffffff !important;
    .header__logo {
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block !important;
      }
    }
    .header__btn {
      span {
        color: #ffffff;
        padding: 0;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  #header {
    .header__group {
      &:nth-child(1) {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 499px) {
  #header {
    .header__inner {
      gap: 1rem;
    }
    .header__logo {
      width: 8.75rem;
    }
    .header__phone {
      font-size: 0.875rem;
    }
  }
}